$breakpoints: (
    mobileS: 320px,
    mobileM: 375px,
    mobileL: 425px,
    mobileXL: 508px,
    tablet: 768px,
    laptop: 1024px,
    laptopM: 1195px,
    laptopL: 1350px,
    laptopXL: 1440px,
    desktop: 1600px,
);

@mixin media($point) {
    $mobile: "(max-width: 600px)";
    $tablet: "(max-width: 900px)";
    $desktop: "(min-width: 901px) and (max-width: 1200px)";

    @if $point == "mobile" {
        @media #{$mobile} {
            @content;
        }
    } @else if $point == "tablet" {
        @media #{$tablet} {
            @content;
        }
    } @else if $point == "desktop" {
        @media #{$desktop} {
            @content;
        }
    }
}
